import styles from './order-details.module.css';
import orderDetailOkImage from '../../images/orderDetailOk.svg'
import { FC } from 'react';

interface IProps {
    orderNum?: string;
}

export const OrderDetails: FC<IProps> = (props) => {
    return (
        <div className={styles.centerText}>
            <p className={`${styles.glow} text text_type_digits-large mb-8`}>{props.orderNum}</p>
            <p className='text text_type_main-medium mb-15'> </p>
            <div className='mb-15'>
                <img src={orderDetailOkImage} alt='' />
            </div>
            <p className='text text_type_main-small mb2'>   </p>
            <p className='text text_type_main-small text_color_inactive mb-15'>    </p>
        </div>
    );
}